@import "~Styles/colors.module";

.exhibition_details_container {
  padding: 25px 0px 30px 0px;
  .exhibition_banner_container {
    align-self: center;
    width: 997px;
    height: 255px;
    align-items: flex-end;
    padding: 18px 15px 18px 28px;
    position: relative;

    .overlay_gradient {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 0;

      background-color: rgba($color-dark, 0.4);
    }

    .banner_content {
      flex: 1;
      color: white;
      z-index: 1;
      max-width: 604px;
      margin-right: auto;

      .exhibition_name {
        font-family: PlayfairDisplay;
        font-size: 36px;
        font-weight: 900;
      }

      .exhibition_description {
        opacity: 0.9;
        font-size: 14px;
        margin: 10px 0px 15px;
      }

      .exhibition_date {
        font-size: 12px;
        color: $color-green-apple;
      }
    }
  }

  .full_description_container {
    align-self: stretch;
    min-height: 205px;
    color: $color-dark;
    border-bottom: solid 1px #ededed;

    .title {
      font-size: 12px;
      font-weight: bold;

      margin-bottom: 14px;
    }

    .left_container {
      flex: 1;
      border-right: solid 1px #ededed;
      padding: 14px;
      padding-left: 43px;

      .description {
        font-size: 14px;
        font-weight: 300;
      }
    }

    .right_container {
      flex: 1;
      padding: 14px;

      .date {
        opacity: 0.9;
        font-size: 10px;
      }
    }
  }

  .product_list_container {
    flex-flow: wrap;
    padding-left: 35px;
  }

  .category_list_container {
    justify-content: flex-start;
    padding-left: 35px;
  }
}

.overview {
  font-size: 12px;
  font-weight: bold;
  color: $color-dark;
}
