@import '~Styles/colors.module';

.orders_page_container {
  padding: 40px 40px 0px;

  .box_container {
    align-self: stretch;
    flex-flow: wrap;
    color: $color-dark;
    justify-content: space-around;
    margin-bottom: 40px;

    .box {
      width: 231px;
      height: 184px;
      border-radius: 2px;
      border: solid 1px rgba(212, 213, 217, 0.5);
      margin-top: 10px;
      
      .title {
        font-size: 40px;
        font-weight: bold;
        text-align: center;
        margin-bottom: 10px;
      }

      .description {
        opacity: 0.6;
        font-size: 16px;
        font-weight: bold;
        text-align: center;
      }
    }
  }
}
